import React,{Component}from 'react';
import Loginform from './components/Loginform';
import Form from './components/Form';
import './App.css';

class App extends Component {
  state={
    step: 1
  }
  nextPage=()=>{
    const{step}=this.state;
    this.setState({
      step: step + 1
    });
  }
  showPage=()=>{
    const{step}=this.state;
    if(step === 1){
      return(<Loginform 
        nextPage={this.nextPage}/>);
    }
    if(step === 2){
      return (<Form/>);
    }
  }
  render(){
    const{step}=this.state;
    return (
      <>  
        {this.showPage()}
      </>
    );
  }
}

export default App;
